.overlay {
    position: fixed;
    inset: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(0, 0, 0, 0.8);
    backdrop-filter: blur(12px);
    z-index: var(--z-modal);
    padding: var(--space-4);
}

.modal {
    position: relative;
    width: 100%;
    max-width: 500px;
    max-height: 90vh;
    overflow-y: auto;
    background: var(--bg-dark-secondary);
    border: 1px solid var(--border-subtle);
    border-radius: var(--radius-2xl);
    padding: var(--space-8);
}

.closeButton {
    position: absolute;
    top: var(--space-4);
    right: var(--space-4);
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(255, 255, 255, 0.05);
    border: none;
    border-radius: 50%;
    color: var(--text-muted);
    cursor: pointer;
    transition: all var(--transition-fast);
}

.closeButton:hover {
    background: rgba(255, 255, 255, 0.1);
    color: var(--text-primary);
}

/* Progress */
.progress {
    display: flex;
    justify-content: center;
    gap: var(--space-2);
    margin-bottom: var(--space-8);
}

.progressDot {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: var(--bg-dark-tertiary);
    transition: all var(--transition-normal);
}

.progressDot.active {
    width: 24px;
    border-radius: var(--radius-full);
    background: linear-gradient(90deg, var(--primary-600), var(--accent-pink));
}

/* Step Content */
.stepContent {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    min-height: 320px;
}

.welcomeEmoji,
.stepEmoji {
    font-size: 4rem;
    margin-bottom: var(--space-4);
}

.stepTitle {
    font-size: 1.75rem;
    font-weight: 800;
    font-family: var(--font-display);
    color: var(--text-primary);
    margin: 0 0 var(--space-2) 0;
}

.stepDescription {
    font-size: 1rem;
    color: var(--text-secondary);
    margin: 0 0 var(--space-6) 0;
    max-width: 350px;
}

.xpPreview {
    display: inline-flex;
    align-items: center;
    gap: var(--space-2);
    padding: var(--space-2) var(--space-4);
    background: rgba(139, 92, 246, 0.15);
    border: 1px solid rgba(139, 92, 246, 0.3);
    border-radius: var(--radius-full);
    font-size: 0.875rem;
    color: var(--primary-300);
}

.xpPreview strong {
    color: var(--accent-pink);
}

/* Name Input */
.nameInput {
    width: 100%;
    max-width: 300px;
    padding: var(--space-4);
    font-size: 1.125rem;
    font-family: var(--font-body);
    text-align: center;
    background: var(--bg-dark-tertiary);
    border: 2px solid var(--border-subtle);
    border-radius: var(--radius-lg);
    color: var(--text-primary);
    outline: none;
    transition: all var(--transition-fast);
}

.nameInput:focus {
    border-color: var(--primary-500);
    box-shadow: 0 0 20px rgba(139, 92, 246, 0.2);
}

.nameInput::placeholder {
    color: var(--text-muted);
}

/* Grade Grid */
.gradeGrid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: var(--space-3);
    width: 100%;
    max-width: 360px;
}

.gradeButton {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: var(--space-1);
    padding: var(--space-3);
    background: var(--bg-dark-tertiary);
    border: 2px solid var(--border-subtle);
    border-radius: var(--radius-lg);
    cursor: pointer;
    transition: all var(--transition-fast);
}

.gradeButton:hover {
    border-color: var(--primary-400);
}

.gradeButton.selected {
    border-color: var(--primary-500);
    background: rgba(139, 92, 246, 0.15);
    box-shadow: 0 0 20px rgba(139, 92, 246, 0.2);
}

.gradeEmoji {
    font-size: 1.5rem;
}

.gradeLabel {
    font-size: 0.8125rem;
    font-weight: 600;
    color: var(--text-secondary);
}

.gradeButton.selected .gradeLabel {
    color: var(--text-primary);
}

/* Interests Grid */
.interestsGrid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: var(--space-3);
    width: 100%;
    margin-bottom: var(--space-4);
}

.interestButton {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: var(--space-2);
    padding: var(--space-4);
    background: var(--bg-dark-tertiary);
    border: 2px solid var(--border-subtle);
    border-radius: var(--radius-lg);
    cursor: pointer;
    transition: all var(--transition-fast);
}

.interestButton:hover {
    border-color: var(--accent, var(--primary-400));
}

.interestButton.selected {
    border-color: var(--accent, var(--primary-500));
    background: color-mix(in srgb, var(--accent, var(--primary-500)) 15%, transparent);
}

.interestEmoji {
    font-size: 1.75rem;
}

.interestLabel {
    font-size: 0.75rem;
    font-weight: 600;
    color: var(--text-secondary);
}

.interestButton.selected .interestLabel {
    color: var(--text-primary);
}

.checkmark {
    position: absolute;
    top: -6px;
    right: -6px;
    width: 20px;
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--accent, var(--primary-500));
    border-radius: 50%;
    font-size: 0.75rem;
    font-weight: bold;
    color: white;
}

.selectionCount {
    font-size: 0.875rem;
    color: var(--text-muted);
}

/* Goals Grid */
.goalsGrid {
    display: flex;
    flex-direction: column;
    gap: var(--space-3);
    width: 100%;
}

.goalButton {
    position: relative;
    display: flex;
    align-items: center;
    gap: var(--space-4);
    padding: var(--space-4);
    background: var(--bg-dark-tertiary);
    border: 2px solid var(--border-subtle);
    border-radius: var(--radius-lg);
    cursor: pointer;
    transition: all var(--transition-fast);
    text-align: left;
}

.goalButton:hover {
    border-color: var(--primary-400);
}

.goalButton.selected {
    border-color: var(--primary-500);
    background: rgba(139, 92, 246, 0.15);
}

.goalEmoji {
    font-size: 2rem;
    flex-shrink: 0;
}

.goalText {
    flex: 1;
    display: flex;
    flex-direction: column;
}

.goalLabel {
    font-size: 1rem;
    font-weight: 600;
    color: var(--text-primary);
}

.goalDescription {
    font-size: 0.8125rem;
    color: var(--text-muted);
}

/* Navigation */
.navigation {
    display: flex;
    justify-content: center;
    gap: var(--space-3);
    margin-top: var(--space-8);
}

.startButton {
    min-width: 160px;
}

/* Responsive */
@media (max-width: 480px) {
    .modal {
        padding: var(--space-6);
    }

    .gradeGrid {
        grid-template-columns: repeat(3, 1fr);
    }

    .interestsGrid {
        grid-template-columns: repeat(3, 1fr);
    }

    .interestEmoji {
        font-size: 1.5rem;
    }

    .interestLabel {
        font-size: 0.6875rem;
    }
}